@import url(https://fonts.googleapis.com/css?family=Noto+Serif+KR);
body{
	font-family: 'Noto Serif KR', sans-serif;
}
@charset "UTF-8";

.main-visual-wrapper{
  width: 40%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 2px 0px 4px rgba(0,0,0,0.2);
  z-index: 1;
  background-color: #fff;
}

.main-visual {
  position: absolute;
  top: 50%;
  right: 50px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: right;
}

.main-visual h1 {
  font-size: 70px;
}
.main-visual p {
  color: #aaa;
  margin-right: 10px;
}
.book-info{
  width: 280px;
  position: absolute;
  left: 0;
  height: 100%;
  border-right: 0.3px solid rgba(0,0,0,0.5);
  overflow: hidden;
}
.book-info:before{
  content: '';
  width: 110%;
  height: 110%;
  position: absolute;
  left: -10px;
  top: -10px;
  /* background-image: url(https://misc.ridibooks.com/cover/371001275/xxlarge);
  background-size: cover;
  filter: blur(50px); */
}

.book-info-title{
  position: relative;
  margin-top: 60px;
  padding: 0 20px;
  text-align: right;
}
.book-info-title:after{
  content: '';
  width: 80%;
  height: 1px;
  position: absolute;
  bottom: -8px;
  right: 15px;
  background-color: rgba(0,0,0,0.2);
}
.book-info-author{
  position: relative;
  text-align: right;
  padding: 0 20px;
}

.book-info-calendar{
  position: absolute;
  left: 10px;
  bottom: 10px;
  width: 100%;
  /* background-color: rgba(255, 255, 255, 0.6); */
}

/* 코멘트 영역 */
.book-comment{
  float: right;
  width: calc(100vw - 280px);
  height: 100vh;
  padding: 0 20px;
  /* background-image: url(https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/ENwbI9D3xilj9k3k8/nature-old-paper-texture-or-background-video-animation_sacheeom__F0000.png); */
}
.book-list-wrapper {
  position: absolute;
  width: calc(60% + 20px);
  right: 0;
  top: 0;
  background-color: #007bff;
}

.book-list {
  padding: 0 20px 0 0;
}

.book-wrapper a {
  color: inherit;
}
.book-wrapper a:hover {
  color: inherit;
  text-decoration: none;
}

.book {
  padding-left: 20px;
  margin-top: 30px;
  -webkit-transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  box-shadow: 2px 2px 4px rgba(0,0,0,0.2);
}
.book:hover {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

.book-author{
  color: rgba(0,0,0,0.5);  
}
@charset "UTF-8";

ul, ul li{
  text-decoration: none;
  list-style: none;
}
.comment{
  margin-top: 15px;
  padding: 15px;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 5px;
  background-color: #fff;
}
.comment-container{
  padding: 15px;
}

.comment-container.empty{
  margin: 50px 0;
  font-size: 32px;
  text-align: center;
  color: rgba(0,0,0,0.3);
}

.comment-content p{
  white-space: pre-wrap;
}

.comment textarea {
  resize: none;
  overflow: hidden;
  min-height: 80px;
  margin: 15px 0;
  border: 0;
  background-color: rgba(0,0,0,0.04);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.comment-date {
  padding: 20px 20px 0;
  color: rgba(0,0,0,0.3);
  font-size: 22px;
}
